body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    overflow-y: scroll;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none; 
    margin: 0; 
}

.hrbtn{
  float: right !important;
  display: flex;
  position: relative;
 justify-content: flex-end; 
 align-items: flex-end;
 margin-right: 30px;
margin-top: -30px;
color: #ffffff;

}
@media screen and (max-width: 768px) {
  .hrbtn{
    float: right !important;
    position: relative;
   justify-content: flex-end; 
   align-items: flex-end;
  
  }
}

.col-9
{
  -webkit-box-flex:0;
  -ms-flex:0 0 75%;
  flex:0 0 75%;
  max-width:75%
}
InputBase{
  border: 1px solid #ccc;
  font-size: 12px;
  height: 30px;
  padding: 4px 8px;
  position: absolute;
  width: 50%;
}
InputBase :focus{
  outline:none;
}
.searched{
  float: left;
  height: 50px;
  margin: 0 8%;
  position: relative;
  width: 84%;
  border-radius: 15px;
  border-color: blue;
  right: 0;
  transition: all .3s ease-in-out;
}
.searched InputBase :focus{
width: 100%;
}

.iconBtn
{
  padding: 10px 15px;
  margin-right: -10px;
  float: right;
  border: 1px solid red;
}
  


.connT{
  padding: 10px;
  display: block;
  margin: 0 auto;
  width: 100%;
  overflow-x: scroll;

}

.inputu{
  width: 86%;
  color: #ffffff;
  justify-content: center;
  padding: 5px 20px;
}
.inputu:focus{
  outline: deepskyblue;
}

.chat-window {
  width: 750px;
  margin: auto;
  border: 1px solid #d5d5d5;
}

.conversation-view {
  width: 100%;
  min-height: 600px;
  padding: 20px 40px;
  background-color: #e5ddd5;
}

.message-box {
  width: 100%;
  padding: 20px 20px;
}

.text-input {
  width: 100%;
  border: none;
  padding: 5px;
}

.chat-bubble {
  font-size: 20px;
  margin-bottom: 20px;
  width: 100%;
  display: flex;
}

.chat-bubble.ai {
  justify-content: flex-end;
}

.chat-bubble.ai .chat-content {
  background-color: #4c9689;
}

.chat-content {
  display: inline-block;
  padding: 8px 15px;
  background-color: #0747a7;
  color: white;
  border-radius: 10px;
}