

.App {
  text-align: center;
}
::-moz-selection {
  /* Code for Firefox */
  color: #fff;
  background: #55c57a;
}

::selection {
  color: #fff;
  background: #55c57a;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #ffffff;
}
::-webkit-scrollbar-thumb {
  color: #3e3d42;
  

  border-radius: 3px;
}
