  overflow-x: hidden;
  overflow-y: hidden;
  width: 100%;
} */

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.searcharea{
  max-height: 250px;
  height: 206px;
  margin-top: -20px;
  background-color: #000033;
  overflow-y: scroll;
  border: 1px solid #ffffff;
  overflow-x: scroll;
  box-shadow: 0 30px 30px rgba(108, 219, 134, 0.3);
}
.fullTab{
  max-height: 400px;
  height: 370px;
  background-color: #000033;
  padding: 0px 10px;

  overflow-x: scroll;
  overflow-y: scroll;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  box-shadow: 0 30px 30px rgba(108, 219, 134, 0.3);
}

.chatbox{
  width: 350px;
  
  margin: 0 20px 0 0;
  position: relative;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, .2);
  display: flex;
  flex-flow: column;
  overflow-y: scroll;
  overflow-x: scroll;
  border-radius: 10px 10px 0 0;
  background-color: #ffffff;
  bottom: 0;
  transition: .1s ease-out;
}

.conversation-view {
  width: 100%;
  min-height: 320px;
  padding: 1px;
 color: #ffffff;
}

.message-box {
  flex: 0 1 40px;
  overflow-y: scroll;
  overflow-x: scroll;
  width: 100%;
  padding: 10px;
  position: relative;
}

.chater {
  background: none;
  align-items: center;
  position: fixed;
  font-size: 11px;
  height: 17px;
  margin-top: 100px;
  margin-bottom: -40px;
  width: 97%;
}

.chat-bubble {
  font-size: 20px;
  margin-bottom: 20px;
  width: 100%;
  display: flex;
}

.chat-bubble.ai {
  justify-content: flex-end;
}

.chat-bubble.ai .chat-content {
  background-color: #4c9689;
}

.chat-content {
  display: inline-block;
  padding: 8px 15px;
  overflow-y: scroll;
  overflow-x: scroll;
  background-color: #537895;
background-image: linear-gradient(315deg, #537895 0%, #09203f 74%);
  color: white;
  border-radius: 10px;
}
.bothead{
  display: flex;
  position: fixed;
  height: 70px;
  width: 350px;
  background-color: #130f40;
background-image: linear-gradient(315deg, #130f40 0%, #000000 74%);
  margin-bottom: 20px;
  padding: 10px;
 
}

.column {
  float: left;
  padding: 10px;

}

.one{
  width: 33.33%;
}

.two{
  width: 62.66%;
}
/* Clear floats after the columns */
.rowl:after {
  content: "";
  display: table;
  clear: both;
}

@media screen and (max-width: 768px) {
  .column{
    float: none;
  }
  
  .one{
    width: 100%;
  }
  
  .two{
    width: 100%;
  }
  .rowl:after {
    content: "";
    display: table;
    position: relative;
  }
  
}

.glass{
  height: 100px;
  width: 240px;
background-color: #000033;
  position: relative;
  z-index: 1;
  color: #ffffff;
  overflow: hidden;
  margin-top: -12px;
  margin: 0 auto;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  box-shadow: 0 30px 30px rgba(108, 219, 134, 0.3);
}
@media screen and (max-width: 768px) {

 .hrf{
   margin-top: 25px;
 }
}
.sds{
  margin-top: -5px;
  position: relative;
 
}
